.theme-switch {
  transition: color 150ms ease-in 0;
  align-items: center;
  display: flex;
  position: absolute;
  top: 14px;
  right: 20px;
}

.theme-switch__icon {
  color: #000;
  height: 16px;
  margin-left: 6px;
  margin-right: 6px;
}

.switch {
  width: 40px;
  height: 20px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.switch__input, .switch__label {
  position: absolute;
  top: 0;
  left: 0;
}

.switch__input {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
}

.switch__input:checked + .switch__label {
  background-color: #d3d3d4;
}

.switch__input:checked + .switch__label + .switch__marker {
  left: calc(100% - 20px);
}

.switch__input:focus + .switch__label, .switch__input:active + .switch__label {
  box-shadow: 0 0 0 3px rgba(192, 33, 41, .2);
}

.switch__label {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #464646;
  transition: background-color .15s;
}

.switch__marker {
  width: 16px;
  height: 16px;
  z-index: 2;
  pointer-events: none;
  will-change: left;
  background-color: #fff;
  border-radius: 50%;
  transition: left .2s;
  position: relative;
  left: 3px;
  right: 3px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

